.Admin__password {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 200px;

    > input[type="password"] {
        height: 50px;
        border-radius: 5px;
        width: 50%;
        font-size: 20px;
        text-align: center;
    }
}

.Admin__usersContainer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.Admin__user {
    flex: 1;
    max-width: 33.333%;
    background: white;
    margin: 20px;
    border-radius: 4px;
    border: 2px solid;
    padding: 10px;
}

.Admin__userLine {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}

.Admin__userData {
    flex: 1;
}